.event-holder {
  background-image: url(img/default1.jpg);
  background-size: cover;
  width: 300px;
  height: 175px;
  margin-left: 5px;
  margin-right: 5px;
  display: inline-flexbox;
  margin-top: 8px;
  background-color: cadetblue;
  border-radius: 3px;
}
.event-holder:hover {
opacity: 0.8;
}
.event {
color: #fff;
width: 300px;
height: 175px
}
.event-name {
  margin: 0;
  vertical-align: middle;
  color: #000;
  float: left;
}

.modal {
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.4);
  position: fixed;
  z-index: 2;
  left: 0;
  top: 0;
}
.modal-content {
  background-color: #fefefe;
  margin: 1% auto;
  height: 95vh;
  padding: 20px;
  border: 1px solid #888;
  width: 80%;
  border-radius: 10px;
}
.the-x {
  float: right;
  font-weight: bold;
  height: 40px;
  width: 40px;
  border-radius: 50%;
  padding-top: 10px;
  margin-top: 0;
}
.the-x:hover {
  cursor: pointer;
  background-color: rgb(230, 230, 230);
  transition-duration: 300ms;
  color: rgb(75, 75, 75);
}
.modal-img {
  background-image: url(img/default1.jpg);
  background-size: cover;
  width: 45%;
  height: 45%;
  border-radius: 5px;
  margin: 15px;
  margin-bottom: 0;
  display: inline-block;
}
.modal-event-info {
  float: right;
  margin-top: 10px;
  margin-right: 100px;
  width: 400px;
  text-align: left;
}
.modal-event-name {
  font-weight: bold;
}
.modal-event-location {
  font-size: 14px;
  color: #000;
  text-align: left;
  display: block;
  margin-bottom: 5px;
}
.modal-event-date {
  font-size: 14px;
  color: #000;
  float: left;
  margin-top: 5px;
  margin-bottom: 5px;
}
.the-details {
  padding-top: 200px;
}
.modal-event-description {
  float: left;
  font-size: 14px;
  margin-top: 10px;
  width: 45%;
  margin-left: 55px;
  text-align: left;
}
.modal-the-description {
  overflow: scroll;
  height: 275px;
}